.cartContainer {
  max-width: 250px;
}

.borderx {
  border-radius: 15px;
}

.card-titlex {
  font-size: 13px;
}


.botonLimpiar {
  width: 350px;
  background-color: crimson !important;
}

.PrecioTotal {
  font-size: 35px;
  font-weight: normal;
}

.precioTotalNumero {
  font-size: 40px;
  font-weight: bold;
  margin-left: 10px;
}

.boton-eliminar {
  background-color: crimson !important;
  color: #fff;
}

.botonPagar {
  background-color: #9fff6c !important;
  color: rgb(0, 0, 0);
  width: 350px;
  font-weight: bold;
}



.vamosAComprar {
  font-size: 30px;
  font-weight: bold;
  margin-top: 10px;
  margin-bottom: 10px;
  color: #9932cc;
}



.comprar {
  box-shadow: inset 0 0 0 0 #ffffff;
  color: #ffffff;
  padding: 0 .25rem;
  margin: 0 -.25rem;
  transition: color .3s ease-in-out, box-shadow .3s ease-in-out;
}

.comprar:hover {
  color: #fff;
  box-shadow: inset 400px 0 0 0 #ffffff;
  ;
}

/* Presentational styles */
comprar {
  color: #ffffff;
  font-family: 'Poppins', sans-serif;
  font-size: 27px;
  font-weight: 700;
  line-height: 1.5;
  text-decoration: none;
}

.empty-cart {
  width: 90vw;
  margin: 0 auto;
  text-align: center;
  font-family: 'Maison Neue';
  font-weight: 300;
}

svg {
  max-width: 60%;

}

svg #oval,
svg #plus,
svg #diamond,
svg #bubble-rounded {
  animation: plopp 4s ease-out infinite;
}

svg #oval:nth-child(1),
svg #plus:nth-child(1),
svg #diamond:nth-child(1),
svg #bubble-rounded:nth-child(1) {
  animation-delay: -240ms;
}

