@-moz-keyframes plopp {
  0% {
    transform: translate(0, 0);
    opacity: 1;
  }

  100% {
    transform: translate(0, -10px);
    opacity: 0;
  }
}

@-webkit-keyframes plopp {
  0% {
    transform: translate(0, 0);
    opacity: 1;
  }

